import React from 'react';
import style from './Menu.module.scss';

const Menu = () => (
  <nav className={style.nav}>
    <ul className={style.menu}>
      <li className={style.menuItem_accented}> </li>
      <li className={style.menuItem}></li>
      <li className={style.menuItem}></li>
      <li className={style.menuItem}></li>
      <li className={style.menuItem}></li>
      <li className={style.menuItem}></li>
      <li className={style.menuItem}></li>
      <li className={style.menuItem}>  </li>
    </ul>
  </nav>
);

export default Menu;
